import makeStyles from "@mui/styles/makeStyles";
import Container from "@mui/material/Container";
import Stack from "@mui/material/Stack";
import Typography from "@mui/material/Typography";
import Grid from "@mui/material/Grid";
import Button from "@mui/material/Button";
import Paper from "@mui/material/Paper";
import React from "react";
import { useNavigate, useParams } from "react-router-dom";
import Banner from "../../components/Banner";
import BottomNav from "../../components/BottomNav";
import TopNav from "../../components/TopNav";
import Check from "@mui/icons-material/Check";
import { connect } from "react-redux";
import { formatNumber } from "../../helpers/common/utility";
import labPng from "../../assets/laboratorium.png";
import { setAppointment } from "../../redux/actions";
const useStyles = makeStyles(() => ({
  textRoll: {
    position: "relative",
    paddingBottom: "128px",
    paddingTop: "16px",
  },
}));
const list = ["Narkoba", "Vaksin", "Makan"];
const PaketDetailPage = (props) => {
  const navigate = useNavigate();
  const { statePaket, actSetAppointment } = props;
  const classes = useStyles();
  const { id } = useParams();
  const [produk, setProduk] = React.useState({
    package_name: null,
    total: 0,
    update_at: "",
    list: [],
  });
  React.useEffect(() => {
    if (id && statePaket) {
      let found = statePaket.find((el) => el.ref_pkt_mcu_id === Number(id));
      if (found) {
        setProduk({
          package_name: found.produk.ref_prod_nama,
          update_at: "05-11-2021 09:00",
          total: found.produk_paket.reduce(
            (a, b) => a + b.produk.ref_prod_hargajual,
            0
          ),
          list: found.produk_paket,
        });
      }
    }
  }, [id, statePaket]);
  const handlePilih = () => {
    actSetAppointment({
      layanan: {
        id_poli: 22,
        ref_layanan_nama: "MCU",
        disabled: true,
      },
      dokter: {
        peg_id: "100080",
        nama_dokter: "DOKTER LAB",
        disabled: true,
      },
      total: produk.total,
    });
    navigate("/make-appointment");
  };
  return (
    <Container className={classes.textRoll}>
      <Stack spacing={2}>
        <TopNav type="back" title={"Berita"} click={() => navigate(-1)} />
        <Banner variant="news" src={labPng} />
        <Container>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Stack spacing={0}>
                <Typography variant="subtitle2">
                  {produk.package_name}
                </Typography>
                <Typography variant="h5">
                  {`${formatNumber(produk.total)}*`}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  *Harga dapat berubah sewaktu-waktu
                </Typography>
                <Typography variant="body1">
                  {`updated : ${produk.update_at}`}
                </Typography>
              </Stack>
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" size="small" onClick={handlePilih}>
                Pilih
              </Button>
            </Grid>
          </Grid>
        </Container>
        <Paper
          sx={{
            padding: "16px",
          }}
        >
          <Grid container spacing={1}>
            {produk.list.map((item, index) => {
              return (
                <Grid item xs={12} container spacing={1} key={index + "_list"}>
                  <Grid item>
                    <Check />
                  </Grid>
                  <Grid item>
                    <Typography variant="body1">
                      {item.produk.ref_prod_nama}
                    </Typography>
                  </Grid>
                </Grid>
              );
            })}
          </Grid>
        </Paper>
      </Stack>
      <BottomNav />
    </Container>
  );
};

const mapStateToProps = (state) => {
  return {
    statePaket: state.master.paketMCU,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    actSetAppointment: (body) => dispatch(setAppointment(body)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PaketDetailPage);
